@font-face {
  font-family: "volt";
  src: url("/static/fonts/Voltage Bold.otf"); }

@font-face {
  font-family: "raleway-extrabold";
  src: url("/static/fonts/Raleway-ExtraBold.ttf"); }

.mobile_navigation {
  position: relative;
  width: 100%;
  background: #8fc5bc; }
  .mobile_navigation .hamburger {
    position: relative;
    width: 100%;
    padding: 30px 0; }
    .mobile_navigation .hamburger input[type="checkbox"] {
      display: none; }
    .mobile_navigation .hamburger input:checked ~ .menu {
      display: block; }
    .mobile_navigation .hamburger .hamburger_icon {
      position: absolute;
      right: 24px;
      top: 0;
      margin-top: 19px;
      z-index: 9999; }
    .mobile_navigation .hamburger label {
      cursor: pointer; }
      .mobile_navigation .hamburger label .line {
        background: white;
        height: 2px;
        width: 50px; }
      .mobile_navigation .hamburger label .line:nth-child(2) {
        margin: 6px 0; }
    .mobile_navigation .hamburger .menu {
      padding-top: 60px;
      background: #8fc5bc;
      opacity: 0.9;
      position: absolute;
      top: 0;
      left: 0;
      font-family: "raleway-extrabold";
      color: white;
      font-size: 16px;
      letter-spacing: 3px;
      display: none;
      width: 100%;
      text-align: center; }
      .mobile_navigation .hamburger .menu li {
        border-top: 1px solid #aad1cb;
        padding: 15px 0; }
        .mobile_navigation .hamburger .menu li .about_label:checked ~ .about_dropdown {
          display: block;
          margin-bottom: -15px; }
        .mobile_navigation .hamburger .menu li .act_label:checked ~ .act_dropdown {
          display: block;
          margin-bottom: -15px; }
        .mobile_navigation .hamburger .menu li .join_label:checked ~ .join_dropdown {
          display: block;
          margin-bottom: -15px; }
        .mobile_navigation .hamburger .menu li .about_dropdown,
        .mobile_navigation .hamburger .menu li .act_dropdown,
        .mobile_navigation .hamburger .menu li .join_dropdown {
          display: none;
          margin-top: 15px;
          padding-bottom: 0;
          background: #76b3a8; }
